import './App.css';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import AppBar from './components/AppBar';
import AddPhrase from './features/phrase/AddPhrase';
import PhraseList from './features/phrase/PhraseList';
import { useState } from 'react';

const useStyles = makeStyles((theme) => ({
  root: {
    margin: theme.spacing(1, 0, 2, 1),
    flexGrow: 1,
  },
  body: {
    margin: theme.spacing(1, 0, 2, 1),
  },
}));

function App() {
  const classes = useStyles();
  const [count, setCount] = useState(0);
  const addPhrase = (n) => setCount(n);

  return (
    <div className={classes.root}>
      <AppBar />
      <Grid
        container
        direction='row'
        justify='flex-start'
        alignItems='flex-start'
        spacing={6}
        className={classes.body}
      >
        <Grid container item xs={4} spacing={1}>
          <AddPhrase addPhrase={addPhrase} count={count} />
        </Grid>
        <Grid container item xs={8} spacing={1}>
          <PhraseList count={count} />
        </Grid>
      </Grid>
    </div>
  );
}

export default App;
