import { createSlice } from '@reduxjs/toolkit'

export const phraseSlice = createSlice({
  name: 'phrase',
  initialState: {
    items: [],
  },
  reducers: {
    addPhrase: (state, action) => {
      state.items.push(action.payload);
    },
    setPhrases: (state, action) => {
      state.items = action.payload;
    },
  },
})

export const { addPhrase, setPhrases } = phraseSlice.actions

export const selectPhrases = state => state.phrase.items;

export default phraseSlice.reducer