import { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import List from '@material-ui/core/List';
import { makeStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import PhraseItem from '../PhraseItem'
import { db } from '../../../utils/firebase';
import { selectPhrases, setPhrases } from '../phraseSlice';


function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
  paper: {
    position: 'absolute',
    width: 400,
    backgroundColor: 'white',
    border: '2px solid #000',
  },
}));

export default function PhraseList() {
  const phrases = useSelector(selectPhrases);
  const [phrase, setPhrase] = useState();
  const classes = useStyles();
  const dispatch = useDispatch();
  
  const [open, setOpen] = useState(false);

  const handleOpen = (phrase) => {
    setPhrase(phrase);
    setOpen(true);
  };

  const handleDelete = async (phrase) => {
    await db.collection('phrases').doc(phrase.id).delete();
    return db.collection('phrases')
      .orderBy('createdAt', 'desc')
      .limit(1000)
      .get()
      .then((querySnapshot) =>
        querySnapshot.docs.map((item) => ({ ...item.data(), id: item.id }))
      ) // .then(console.log);
      .then((phrases) => dispatch(setPhrases(phrases)));
  };

  const handleClose = () => {
    setOpen(false);
  };

  
  const [modalStyle] = useState(getModalStyle);

  const body = (
    <div style={modalStyle} className={classes.paper}>
      <h2 id="simple-modal-title">{phrase && phrase.title}</h2>
      <p id="simple-modal-description">
        {phrase && phrase.meaning}
      </p>
    </div>
  );

  useEffect(() => {
    db.collection('phrases')
      .orderBy('createdAt', 'desc')
      .limit(1000)
      .get()
      .then((querySnapshot) =>
        querySnapshot.docs.map((item) => ({ ...item.data(), id: item.id }))
      ) // .then(console.log);
      .then((phrases) => dispatch(setPhrases(phrases)));
  }, []);

  return (
    <List component='nav' className={classes.root} aria-label='secondary mailbox folders'>
      {phrases.map((phrase) => (
        <PhraseItem phrase= {phrase} key={phrase.id} handleOpen={handleOpen} handleDelete={handleDelete}/>
      ))}
      
    <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {body}
      </Modal>
    </List>
  );
}
